@media screen and (max-width:1080px){
    .box{
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .background{
        border: 1px solid #0088f7;
        width: 70%;
        padding:2vh;
        justify-content: center;
        border-radius: 7vh;
        height: 90vh;
    }
    .background:hover{
        transition: 1s;
        box-shadow:  0 0 10px #619ecf, 0 0 0px #619ecf, 0 0 30px #619ecf;
    }
    .items{
        padding-top: 1.5vh;
    }
    .numInput{
        border-style: none;
        border-bottom:2px ridge #0088f7;
        border-radius: 1000px;
        padding: 0.5vh;
        width: 30vh;
        font-family: Verdana, Geneva, Tahoma, sans-serif;
        color: transparent;
        -webkit-text-stroke: 1px #0088f7;
        
    }
    .numInput:hover{
        
        box-shadow:0px 0px 2px #0088f7;
    }
    .fonts{
        font-family: Verdana, Geneva, Tahoma, sans-serif;
        color: transparent;
        -webkit-text-stroke: 1px #0088f7;
        
        padding: 0.5vh;
    }
    .fontsTittle{
        
        padding: 0.5vh;
        font-family: Verdana, Geneva, Tahoma, sans-serif;
        color: transparent;
        -webkit-text-stroke: 2px #0088f7;
        
    }
    .fontsTittle:hover{
        transition: 1s;
        text-shadow: 0 0 10px #0088f7, 0 0 40px #0088f7, 0 0 80px #0088f7;
    }
    
    .errorStyle{
        color:#f63900;
        font-size: 2vh;
        font-family: monospace;
    }
    .fontErr{
        font-family: Verdana, Geneva, Tahoma, sans-serif;
        color: transparent;
        -webkit-text-stroke: 1px #0088f7;
        font-size: 2vh;
        padding: 0.5vh;
    }
}




