@media screen and (max-width: 1080px) {

    .button{
        margin-top: 30px;
        padding: 15px 30px;
        text-decoration: none;
        font-size: 2rem;
        color:white;
        text-align: center;
        position: relative;
        border-radius: 9px;
        width: 300px;
        letter-spacing: 4px;
        overflow: hidden;
        transition-delay: 10s;
        transition: 0.4s;
    }


    .button:hover{
        background-color: #619ecf;
        box-shadow: 0 0 10px #619ecf, 0 0 40px #619ecf, 0 0 80px #4545c7;
        transition: 2s;
        cursor: pointer;
    }
    
    .button span{
        position: absolute;
        display: block;
    }
    
    
    /* PARTE SUPERIOR */
    .span1{
        top: 0px;
        left: -100%;
        width: 100%;
        height: 2px;
        background: linear-gradient(90deg, transparent, #7545c7);
    }
    
    .button:hover .span1{
        left: 100%;
        transition: 1s;
    }
    
    
    
    
    
    /* PARTE LATERAL DERECHO */
    
    .span3{
        bottom: 0px;
        right: -100%;
        width: 100%;
        height: 2px;
        background: linear-gradient(270deg, transparent, #7f45c7);
    }
    
    .button:hover .span3{
        right: 100%;
        transition: 1s;
        transition-delay: 0.5s;
    }
    
    
    
    /* PARTE INFERIOR */
    .span2{
        top: -100%;
        right: 0;
        width: 2px;
        height: 100%;
        background: linear-gradient(180deg, transparent, #a945c7);
    }
    
    .button:hover .span2{
        top: 100%;
        transition: 1s;
        transition-delay: 0.25s;
    }
    
    
    
    /* PARTE LATERAL IZQUIERDO */
    .span4{
        bottom: -100%;
        left: 0;
        width: 2px;
        height: 100%;
        background: linear-gradient(360deg, transparent, #a945c7);
    }
    
    .button:hover .span4{
        bottom: 100%;
        transition: 1s;
        transition-delay: 0.75s;
    }
    
}
